import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import enIndex from '../config/index/en';
import krIndex from '../config/index/kr';
import enAbout from '../config/about/en';
import krAbout from '../config/about/kr'
import enContact from '../config/contact/en';
import krContact from '../config/contact/kr';

i18n.use(initReactI18next).init({
  debug: false,
  resources: {
    en: {
      translation: {
        index: enIndex,
        about: enAbout,
        contact: enContact
      },
    },
    kr: {
      translation: {
        index: krIndex,
        about: krAbout,
        contact: krContact
      },
    }
  },
  lng: 'kr',
  fallbackLng: false,
  returnEmptyString: false,
})

export default i18n;
